label{
    display: inline-block;
    margin: 10px 0;
}
input{
    border: 1px solid #dedede;
    border-radius: 2px;
    display: block; /* การทำให้อยู่คนละบรรทัด */
    font-size: 16px;
    padding: 10px;
    width: 100%;
}

input:focus{
    outline: none;
}

.btn{
    background-color: rgb(240, 194, 240);
    color: #531a4c; 
    cursor: pointer;
    display: block;
    margin: 10px 0 30px; 
    padding: 10px;
    font-size: 16px;
    width: 100%;
}